        'Â∞ÑÊâãÂ∫ß': ['ÁôΩÁæäÂ∫ß', 'ÁãÆÂ≠êÂ∫ß', 'Â§©Áß§Â∫ß', 'Ê∞¥Áì∂Â∫ß'],
